 .wptravel-block-trip-rating{

    .wp-travel-average-review {
        font-family: "Font Awesome 5 Free";
        font-size: 1em;
        height: 1em;
        line-height: 1;
        overflow: hidden;
        position: relative;
        width: 5.5em;
        display: inline-block;
        word-break: normal;
        span {
            float: left;
            left: 0;
            overflow: hidden;
            padding-top: 1.5em;
            position: absolute;
            top: 0;
            color: #f9a032;
            font-weight: 900;
        }
        span::before {
            content: "";
            left: 0;
            position: absolute;
            top: 0;
        }
    }

    .wp-travel-average-review:before {
        color: #f9a032;
        content: "";
        float: left;
        left: 0;
        position: absolute;
        top: 0;
    }

 }

